// soundPlayer.play();

let currentPlayedNote = 'C4';

const sounds = ['C4', 'D4', 'E4', 'F4', 'G4', 'A4', 'B4'];

const playRandomSound = () => {
  let whichSoundToPlayNext = Math.floor(Math.random() * sounds.length);
  let currentSound = sounds[whichSoundToPlayNext];

  const soundPlayer = new Audio(`./${currentSound}.mp3`);
  soundPlayer.play();
  currentPlayedNote = currentSound;

  setTimeout(() => soundPlayer.pause(), 2000);
};

for (let sound of sounds) {
  let element = document.getElementById(sound);
  element?.addEventListener('click', () => {
    if (currentPlayedNote === sound) {
      element.setAttribute('class', 'correct');
      setTimeout(() => element.setAttribute('class', ''), 500);
    } else {
      let correctElement = document.getElementById(currentPlayedNote);
      correctElement?.setAttribute('class', 'error');
      setTimeout(() => correctElement?.setAttribute('class', ''), 500);
    }

    playRandomSound();
  });
}
